import React, { useState, useEffect} from 'react';
import { useLanguagePreference } from '../../contexts/LanguagePreferenceContext'; 

const languageOptions = [
  { code: 'devanagari', name: 'Devanagari' },
  { code: 'itrans', name: 'ITRANS' },
  { code: 'iast', name: 'IAST' },
  { code: 'tamil_superscripted', name: 'Tamil' },
  { code: 'malayalam', name: 'Malayalam' },
  { code: 'telugu', name: 'Telugu' },
  { code: 'gujarati', name: 'Gujarati' },
  { code: 'gurmukhi', name: 'Gurmukhi' },
  { code: 'oriya', name: 'Oriya' },
];

function Settings() {
  const { preferredLanguage, setPreferredLanguage } = useLanguagePreference();
  const [selectedLanguage, setSelectedLanguage] = useState(preferredLanguage);

  useEffect(() => {
    // Save language preference to localStorage or your preferred storage mechanism
    localStorage.setItem('preferredLanguage', selectedLanguage);
    setPreferredLanguage(selectedLanguage);
    //console.log(`selectedLanguage:${selectedLanguage}`)
  }, [selectedLanguage,setPreferredLanguage]);

  const handleLanguageChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    setSelectedLanguage(event.target.value);
    //console.log(`selectedLanguage:${selectedLanguage}`)
  };

  return (
    <div>
      <h2>Language Preferences</h2>
      <p>Choose the language for transliteration:</p>
      <select value={selectedLanguage} onChange={handleLanguageChange}>
        {languageOptions.map(option => (
          <option key={option.code} value={option.code}>
            {option.name}
          </option>
        ))}
      </select>
    </div>
  );
}

export default Settings;