import React from "react";
import { transliterateText } from '../../utils/transliterationUtils';

interface JsonDataTableProps {
  data: (string | number)[][];
  className?: string;
  highlightColor?: string;
  highlightColumns?: number[];
  wrapColumns?: number[];
  striped?: boolean;
  hoverEffect?: boolean;
  maxWidth?: "screen" | "full" | string;
  fontSize?: "sm" | "base" | "lg" | "xl" | "2xl";
  preferredLanguage:string;
  sourceLanguage:string;
}

const JsonToHtmlTable: React.FC<JsonDataTableProps> = ({
  data,
  className = "",
  highlightColor = "bg-amber-50",
  highlightColumns = [],
  wrapColumns = [],
  striped = true,
  hoverEffect = true,
  maxWidth = "screen",
  fontSize = "2xl",
  preferredLanguage,
  sourceLanguage,
}) => {
  if (!Array.isArray(data) || data.length < 1) {
    return (
      <div className="p-2 text-red-500 bg-red-50 rounded">
        Invalid data: Must be a non-empty array
      </div>
    );
  }

  const [headers, ...rows] = data;

  const renderCellContent = (content: string | number): { __html: string } => {
    let html = String(content)
      .replace(/<br\s*\/?>/gi, "<br/>")
      .replace(/\n/g, "<br/>");
  
      if (typeof content === 'string' && preferredLanguage !== 'deva' && window.Sanscript) {
      // Transliterate the entire HTML string, including the <br/> tags
      html = transliterateText(html, sourceLanguage, preferredLanguage);
    }
    return { __html: html };
  };

  // Function to safely render HTML content with line breaks
  /*
  const renderCellContentOld = (content: string | number) => {
    const html = String(content)
      // Ensure <br/> tags are properly closed
      .replace(/<br\s*\/?>/gi, "<br/>")
      // Convert newlines to <br/> if needed
      .replace(/\n/g, "<br/>");

    return { __html: html };
  };
  */

  const maxWidthClass =
    maxWidth === "screen"
      ? "max-w-screen"
      : maxWidth === "full"
      ? "w-full"
      : maxWidth;

  return (
    //<div className={`overflow-x-auto rounded-lg border border-gray-200 ${maxWidthClass} ${className}`} style={{ overflowY: 'auto', maxHeight: '400px' }}>
    <div
      className={`overflow-x-auto rounded-lg border bg-gray-300 ${maxWidthClass} ${className}`}
    >
      <table className="min-w-full divide-y divide-gray-200 font-devanagari">
        <thead className="bg-gray-50">
          <tr>
            {headers.map((header, colIndex) => (
              <th
                key={`th-${colIndex}`}
                className={`
                  px-4 py-4 text-left text-${fontSize} font-medium text-gray-700  {/* Darker text, more padding */}
                  ${highlightColumns.includes(colIndex) ? highlightColor : ""}
                  sticky top-0 bg-gray-100 {/* Ensure background for sticky header */}
                `}
                dangerouslySetInnerHTML={renderCellContent(header)}
              />
            ))}
          </tr>
        </thead>

        <tbody className="bg-white divide-y divide-gray-200">
          {rows.map((row, rowIndex) => (
            <tr
              key={`row-${rowIndex}`}
              id={`trRowAsAnchor-${rowIndex}`}
              className={`
        ${
          striped && rowIndex % 2 === 0 ? "bg-white" : "bg-gray-100"
        } {/* Slightly more contrast for stripes */}
        ${hoverEffect ? "hover:bg-indigo-50" : ""} {/* More noticeable hover */}
      `}
            >
              {row.map((cell, colIndex) => (
                <td
                  key={`td-${rowIndex}-${colIndex}`}
                  className={`
            px-4 py-3.5 text-${fontSize} align-top {/* Slightly more vertical padding */}
            ${
              wrapColumns.includes(colIndex)
                ? "whitespace-normal break-words max-w-[300px]"
                : "whitespace-nowrap"
            }
            ${highlightColumns.includes(colIndex) ? highlightColor : ""}
          `}
                  dangerouslySetInnerHTML={renderCellContent(cell)}
                />
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default JsonToHtmlTable;
