import React, { useState } from 'react';
import { supabase } from '../../utils/supabase';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext'; 

const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const navigate = useNavigate();
  const { setSession } = useAuth();

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const { data: { session }, error: supabaseError } = await supabase.auth.signInWithPassword({
        email,
        password,
      });

      if (supabaseError) {
        setError(supabaseError.message);
      }
      else if (session) {
        //console.log('Login successful, session:', session);
        setSession(session); // Update AuthContext with the new session
        navigate('/categories', { replace: true });
      }
    } catch (err: any) {
      setError(err.message || 'An unexpected error occurred during login.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-100 py-6 flex flex-col justify-center sm:py-12">
  <div className="relative py-3 sm:max-w-xl sm:mx-auto">
    <div className="absolute inset-0 bg-gradient-to-r from-blue-300 to-blue-600 shadow-lg transform -skew-y-6 sm:skew-x-6 sm:rounded-3xl"></div>
    <div className="relative bg-white px-4 py-10 shadow-lg sm:rounded-3xl sm:px-20">
      <div className="max-w-md mx-auto">
        {/* Page Title */}
        <div className="text-center">
          <h2 className="text-3xl font-extrabold text-gray-900">Login</h2>
          <p className="mt-2 text-sm text-gray-500">-------------</p>
        </div>
        <div>{error && <p className="text-red-500">{error}</p>}</div>
        <div className="divide-y divide-gray-200">
         <form onSubmit={handleLogin}>
          <div className="py-8 text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7">
            <div className="relative">
              <input
                autoComplete="off"
                id="email"
                name="email"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                className="peer w-full min-h-[1em] rounded-md border-2 border-gray-200 px-3 py-2 font-sans text-gray-500 transition-all placeholder-shown:border-gray-200 focus:border-blue-500 focus:outline-none"
                placeholder="Email address"
              />
              <label
                htmlFor="email"
                className="peer-placeholder-shown:h-[28px] peer-placeholder-shown:text-gray-500 absolute top-0 left-3 -translate-y-1/2 bg-white px-2 text-sm font-bold text-blue-500 transition-all peer-focus:-translate-y-1/2 peer-focus:text-blue-500"
              >
                Email address
              </label>
            </div>
            <div className="relative">
              <input
                autoComplete="off"
                id="password"
                name="password"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
                className="peer w-full min-h-[1em] rounded-md border-2 border-gray-200 px-3 py-2 font-sans text-gray-500 transition-all placeholder-shown:border-gray-200 focus:border-blue-500 focus:outline-none"
                placeholder="Password"
              />
              <label
                htmlFor="password"
                className="peer-placeholder-shown:h-[28px] peer-placeholder-shown:text-gray-500 absolute top-0 left-3 -translate-y-1/2 bg-white px-2 text-sm font-bold text-blue-500 transition-all peer-focus:-translate-y-1/2 peer-focus:text-blue-500"
              >
                Password
              </label>
            </div>
            <div className="relative">
              <button
                type="submit"
                className="w-full rounded-md bg-blue-500 px-4 py-2 font-bold text-white shadow-md hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-400 focus:ring-offset-2"
				disabled={loading}
              >
                {loading ? 'Logging in...' : 'Login'}
              </button>
            </div>
            </div>
			</form>
      <p>
        Don't have an account? <button type="button" onClick={() => navigate('/signup')}>Sign Up</button>
      </p>
      <p>
        Forgot your password? <button type="button" onClick={() => navigate('/change-password')}>Change Password</button>
      </p>
          </div>
        </div>
      </div>
    </div>
  </div>

  );
};

export default Login;