import React, { useEffect, useState, useContext } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { supabase } from '../../utils/supabase'; 
import { AuthContext } from '../../contexts/AuthContext';

const AUTH_REDIRECT_FLAG = 'auth_redirect_sankeertan';

const AuthHandler = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const { setSession } = useContext(AuthContext);

   useEffect(() => {
    //console.log('AuthHandler useEffect triggered');
    const token = searchParams.get('token');
    const type = searchParams.get('type');
    const email = searchParams.get('email');
    //console.log('AuthHandler: token, type, email', token, type, email);

    const handleAuthCallback = async (token: string, type: string, identifier: string) => {
      //console.log('handleAuthCallback called with:', token, type, identifier);
      setLoading(true);
      setError(null);
  
      try {
        let res;
        if (type === 'invite' || type === 'signup' || type === 'email_change' || type === 'magiclink') {
          const email = identifier;
          if (!email) {
            setError('Email address is missing from the authentication link.');
            setLoading(false);
            navigate('/login', { replace: true });
            return;
          }
          res = await supabase.auth.verifyOtp({ token, type: type as 'invite' | 'signup' | 'email_change' | 'magiclink', email });
          //console.log('supabase.auth.verifyOtp result:', res);
        } else if (type === 'recover') {
          navigate(`/reset-password?token=${token}`, { replace: true });
          return;
        } else if (type === 'sms') {
          const phone = identifier;
          if (!phone) {
            setError('Phone number is missing from the authentication link.');
            setLoading(false);
            navigate('/login', { replace: true });
            return;
          }
          res = await supabase.auth.verifyOtp({ token, type: 'sms', phone });
        } else {
          setError(`Unsupported authentication type: ${type}`);
          setLoading(false);
          navigate('/login', { replace: true });
          return;
        }
        if (res?.error) {
          setError(res.error.message);
        } else {
          //console.log('Authentication successful!');
          // Get the current session immediately after successful verification
          const { data: { session: currentSession } } = await supabase.auth.getSession();
          if (currentSession) {
            setSession(currentSession); // Update the AuthContext session
          }
          localStorage.setItem(AUTH_REDIRECT_FLAG, 'true');
          navigate('/', { replace: true }); // Redirect to home, App will handle navigation
        }
      } catch (err: any) {
        setError(err.message || 'An unexpected error occurred during authentication.');
      } finally {
        setLoading(false);
      }
    };
    if (token && type) {
      if (type === 'recover') {
        handleAuthCallback(token, type, ''); // Email not needed for recover in this flow
      } else if (type === 'invite' || type === 'signup' || type === 'email_change' || type === 'magiclink') {
        if (email) {
          handleAuthCallback(token, type, email);
        } else {
          setError('Email address is missing from the authentication link.');
          setLoading(false);
          navigate('/login', { replace: true });
        }
      } else {
        handleAuthCallback(token, type, ''); // For other types, email might not be needed
      }
    } else {
      setError('Invalid authentication link.');
      setLoading(false);
      navigate('/login', { replace: true });
    }
  }, [navigate, searchParams, setSession]); 
  if (loading) {
    return <div>Verifying your account...</div>;
  }
  if (error) {
    return <div>Error verifying account: {error}</div>;
  }
  return null;
};

export default AuthHandler;