import React, { createContext, useState, useEffect, useContext, Dispatch, SetStateAction } from 'react';

interface LanguagePreferenceContextType {
  preferredLanguage: string;
  setPreferredLanguage: Dispatch<SetStateAction<string>>;
}

export const LanguagePreferenceContext = createContext<LanguagePreferenceContextType>({
  preferredLanguage: 'deva', // Default to Devanagari
  setPreferredLanguage: () => {}, // Dummy function
});

export const LanguagePreferenceProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  //console.log("LanguagePreferenceProvider - Mounted");

  // Initialize preferredLanguage from localStorage or the default
  const storedLanguage = localStorage.getItem('preferredLanguage');
  //console.log("LanguagePreferenceProvider - storedLanguage:", storedLanguage);
  const [preferredLanguage, setPreferredLanguage] = useState<string>(storedLanguage || 'deva');
  //console.log("LanguagePreferenceProvider - initial preferredLanguage state:", preferredLanguage);

  // Save preferredLanguage to localStorage whenever it changes
  useEffect(() => {
    localStorage.setItem('preferredLanguage', preferredLanguage);
    //console.log("LanguagePreferenceContext - preferredLanguage updated and saved:", preferredLanguage);
  }, [preferredLanguage]);

  //console.log("LanguagePreferenceProvider - current preferredLanguage before providing:", preferredLanguage);
  return (
    <LanguagePreferenceContext.Provider value={{ preferredLanguage, setPreferredLanguage }}>
      {children}
    </LanguagePreferenceContext.Provider>
  );
};

export const useLanguagePreference = () => {
  return useContext(LanguagePreferenceContext);
};