function correctDevanagariAnusvarasBeforeTransliteration(text: string): string {
    const rules = [
        { pattern: /ं([चछजझ])/g, replacement: 'ञ्$1', name: 'Palatal' },
        { pattern: /ं([टठडढ])/g, replacement: 'ण्$1', name: 'Retroflex' },
        //{ pattern: /ं([कखगघ])/g, replacement: 'ङ्$1', name: 'Velar' },
        { pattern: /ं([क])/g, replacement: 'ङ्$1', name: 'Velar' },
        { pattern: /ं([तथदध])/g, replacement: 'न्$1', name: 'Dental' },
        //{ pattern: /ं([पफबभ])/g, replacement: 'म्$1', name: 'Labial' },
        { pattern: /ं([पफ])/g, replacement: 'म्$1', name: 'Labial' },
        //{ pattern: /ं([यरलवशषसह])/g, replacement: 'ङ्$1', name: 'Before semi-vowels/sibilants' },
        { pattern: /ं$/g, replacement: 'म्', name: 'Word-final' },
        { pattern: /ँ/g, replacement: 'म्', name: 'Chandrabindu → म्' }
      ];
      return rules.reduce((str, rule) => {
        const newStr = str.replace(rule.pattern, (m, g1) => {
          //Logger.log(`[${rule.name}] ${m} → ${rule.replacement.replace('$1', g1)}`);
          return rule.replacement.replace('$1', g1);
        });
        return newStr !== str ? newStr : str;
      }, text);
  }
  
function fixMalayalamPostTransliteration(text: string): string {
    const malayalamToEnglishNumbers: { [key: string]: string } = {
      '൦': '0', '൧': '1', '൨': '2', '൩': '3', '൪': '4',
      '൫': '5', '൬': '6', '൭': '7', '൮': '8', '൯': '9'
    };
    return text
      // 3. Fix र् → ർ (सर्व → സർവ)
      .replace(/\u0D30\u0D4D/g, '\u0D7C')
      .replace(/[൦-൯]/g, match => {
        // Explicitly cast 'match' to be a key of malayalamToEnglishNumbers
        return malayalamToEnglishNumbers[match as keyof typeof malayalamToEnglishNumbers] || match;
      });
  }
  function fixTamilPostTransliteration(text: string): string {
    const tamilToEnglishNumbers: { [key: string]: string } = {
      '௦': '0', '௧': '1', '௨': '2', '௩': '3', '௪': '4',
      '௫': '5', '௬': '6', '௭': '7', '௮': '8', '௯': '9'
    };
  
    let processedText = text;
  
    // Replace Tamil numbers
    for (const tamilDigit in tamilToEnglishNumbers) {
      const englishDigit = tamilToEnglishNumbers[tamilDigit];
      // Use a global regex to replace all occurrences of the Tamil digit
      processedText = processedText.replace(new RegExp(tamilDigit, 'g'), englishDigit);
    }
    //console.log(`processedText:${processedText}`)
    return processedText;
  }
  
 function fixTamilToItransPostTransliteration(text: string): string {
    return text
    // 1. Fix nasal consonants (ஞ, ண, ந, ன)
    .replace(/~nja?/g, '~n')     // ஞ → ~n (e.g., அஞ்ச → a~nca)
    .replace(/~nca?/g, '~nca')   // Preserve ஞ்ச → ~nc
    .replace(/[னணந]்?/g, 'n')    // All Tamil nasals → n

    // 2. Fix plosives (stop consonants)
    .replace(/jh/g, 'c')         // ச → c (not jh)
    .replace(/bh/g, 'p')         // ப → p
    .replace(/gh/g, 'k')         // க → k
    .replace(/dh/g, 't')         // த → t

    // 3. Fix vowel endings
    .replace(/([kctp])a(?![aiueoāīūēō])/g, '$1')  // Remove stray 'a's
    .replace(/ா/g, 'ā')          // Long vowels
    .replace(/ி/g, 'i')
    .replace(/ீ/g, 'ī')
    .replace(/ு/g, 'u')
    .replace(/ூ/g, 'ū')
    
    // 4. Fix common clusters
    .replace(/n([ctkp])/g, 'ṉ$1')  // ன்+plosive → ṉ (optional)
    .replace(/ா/g, 'ā')           // Ensure long ā
    .replace(/்ர/g, 'r')         // ர → r
												  


    // STEP 3: Optional vowel normalization (aa→A, ii→I, etc.)
    .replace(/aa/g, 'A')
    .replace(/ii/g, 'I')
    .replace(/uu/g, 'U');
  }

  export const transliterateText = (sourceText: string, sourceLanguage: string, targetLanguage: string): string => {
    let processedSourceText: string = sourceText;
    let transliteratedText: string = sourceText; // Initialize with source text
  
    if (sourceLanguage === "devanagari") {
      processedSourceText = correctDevanagariAnusvarasBeforeTransliteration(processedSourceText);
    } else if (sourceLanguage === "tamil") {
      processedSourceText = processedSourceText.normalize("NFC");
    }
  
    if (window.Sanscript) {
      //console.log(`here001....${sourceLanguage}...${targetLanguage}`);
      transliteratedText = window.Sanscript.t(processedSourceText, sourceLanguage === 'devanagari' ? 'devanagari' : 'itrans', targetLanguage);
  
      if (targetLanguage === "malayalam") {
        transliteratedText = fixMalayalamPostTransliteration(transliteratedText);
      } else if (targetLanguage === "itrans") {
        if (sourceLanguage === "tamil") {
          transliteratedText = fixTamilToItransPostTransliteration(transliteratedText);
        }
      }
      else if(targetLanguage==="tamil" || targetLanguage==="tamil_superscripted"){
        //console.log(`transliterateText:${transliterateText}`);
        transliteratedText=fixTamilPostTransliteration(transliteratedText);
      }
    }
  
    return transliteratedText;
  };
  
  