import React, { createContext, useState, useContext, useEffect,ReactNode } from 'react';

interface OfflineModeContextType {
    isOfflineMode: boolean;
    setIsOfflineMode: (value: boolean) => void;
    isEffectivelyOffline: boolean;
  }

  const OfflineModeContext = createContext<OfflineModeContextType>({
    isOfflineMode: false,
    setIsOfflineMode: () => {},
    isEffectivelyOffline: false,
  });

  export const OfflineModeProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
    const [isOfflineMode, setIsOfflineMode] = useState(false);
    const isEffectivelyOffline = isOfflineMode || !navigator.onLine;
  
    useEffect(() => {
      const handleOnline = () => {
                                                                   
        if (!isOfflineMode) {
          // Optionally update state
        }
      };
  
      const handleOffline = () => {
                                                                   
        if (!isOfflineMode) {
          setIsOfflineMode(true); // Or however you want to handle actual offline
        }
      };
  
      window.addEventListener('online', handleOnline);
      window.addEventListener('offline', handleOffline);
  
      return () => {
        window.removeEventListener('online', handleOnline);
        window.removeEventListener('offline', handleOffline);
      };
    }, [isOfflineMode, setIsOfflineMode]);
  
    return ( // Explicitly return the JSX
      <OfflineModeContext.Provider value={{ isOfflineMode, setIsOfflineMode, isEffectivelyOffline }}>
        {children}
      </OfflineModeContext.Provider>
    );
  };
  
  export const useOfflineMode = () => useContext(OfflineModeContext);