import React, { createContext, useState, useEffect, useCallback, useContext } from 'react';
import { useParams } from 'react-router-dom';

interface Category {
  id: string;
  name: string;
}

interface Book {
  id: string;
  name: string;
  categoryIds: string[];
}

interface BookContextType {
  books: Book[];
  categories: Category[];
  currentBook: Book | null;
  loadBook: (bookId: string) => Promise<void>;
  loading: boolean;
  error: string | null;
}

const BookContext = createContext<BookContextType>({
  books: [],
  categories: [],
  currentBook: null,
  loadBook: async () => {},
  loading: false,
  error: null,
});

interface BookProviderProps {
  children: React.ReactNode;
}

export const BookProvider: React.FC<BookProviderProps> = ({ children }) => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [books, setBooks] = useState<Book[]>([
    { id: 'book1', name: 'The Great Book A', categoryIds: ['cat1', 'cat2'] },
    { id: 'book2', name: 'Another Fine Book B', categoryIds: ['cat2', 'cat3'] },
    { id: 'book3', name: 'Wonderful Book C', categoryIds: ['cat1'] },
  ]);
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [categories, setCategories] = useState<Category[]>([
    { id: 'cat1', name: 'Fiction' },
    { id: 'cat2', name: 'Mystery' },
    { id: 'cat3', name: 'Science Fiction' },
  ]);
  const [currentBook, setCurrentBook] = useState<Book | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const { bookId: routeBookId } = useParams();

  const loadBook = useCallback(async (bookId: string) => {
    setLoading(true);
    setError(null);
    setCurrentBook(null);
    await new Promise(resolve => setTimeout(resolve, 500));
    const book = books.find(b => b.id === bookId);
    setCurrentBook(book || null);
    setLoading(false);
  }, [books, setLoading, setError, setCurrentBook]); 

  useEffect(() => {
    if (routeBookId) {
      loadBook(routeBookId);
    }
  }, [routeBookId, loadBook]);

  const value: BookContextType = {
    books,
    categories,
    currentBook,
    loadBook,
    loading,
    error,
  };

  return (
    <BookContext.Provider value={value}>{children}</BookContext.Provider>
  );
};

export const useBook = () => useContext(BookContext) as BookContextType;