import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { supabase } from "../../utils/supabase";
import { Session } from "@supabase/supabase-js";
import { useOfflineMode } from "../../contexts/OfflineModeContext";

interface UserProfileMenuProps {
  toggleDropdown: () => void;
  isDropdownOpen: boolean;
  openLanguageSettings: () => void; // Receive the open modal function
}

const UserProfile = ({ toggleDropdown,isDropdownOpen, openLanguageSettings }: UserProfileMenuProps) => {
  const [session, setSession] = useState<Session | null>(null);
  const navigate = useNavigate();

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
    });

    supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session);
    });
  }, []);

  /*
  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
  };
  */

  const handleSignOut = async () => {
    const { error } = await supabase.auth.signOut();
    if (error) {
      console.error("Error signing out:", error);
      alert("Error signing out. Please try again.");
    } else {
      //console.log('User signed out successfully.');
      navigate("/login");
    }
  };

  const { isOfflineMode, setIsOfflineMode } = useOfflineMode();

  return (
    <div className="relative inline-block text-left">
      {session ? (
        <div>
          <button
            type="button"
            className="inline-flex w-full justify-center gap-x-1.5 rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50"
            onClick={toggleDropdown}
            aria-expanded={isDropdownOpen}
            aria-haspopup="true"
          >
            {session.user?.email || "User"}
            <svg
              className="-mr-1 h-5 w-5 text-gray-400"
              viewBox="0 0 20 20"
              fill="currentColor"
              aria-hidden="true"
            >
              <path
                fillRule="evenodd"
                d="M5.23 7.21a.75.75 0 011.06.02L10 11.168l3.71-3.938a.75.75 0 111.08 1.04l-4.25 4.5a.75.75 0 01-1.08 0l-4.25-4.5a.75.75 0 01.02-1.06z"
                clipRule="evenodd"
              />
            </svg>
          </button>

          {isDropdownOpen && (
            <div className="absolute right-0 z-10 mt-2 w-40 origin-top-right rounded-md bg-white shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
              <div
                className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                role="menuitem"
              >
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    className="mr-2 form-checkbox h-4 w-4 text-indigo-600 focus:ring-indigo-500 border-gray-300 rounded"
                    checked={isOfflineMode}
                    onChange={(e) => setIsOfflineMode(e.target.checked)}
                  />
                  <span>Offline Mode</span>
                </label>
              </div>

              <div
                className="py-1"
                role="menu"
                aria-orientation="vertical"
                aria-labelledby="user-menu-button"
              >
                <Link
                  to="/sankeertan/1"
                  className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                  role="menuitem"
                  onClick={toggleDropdown}
                >
                  Go to Sankeertan
                </Link>
                <Link
                  to="/categories"
                  className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                  role="menuitem"
                  onClick={toggleDropdown}
                >
                  Go to Categories
                </Link>
                <Link
                  to="/settings"
                  className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                  role="menuitem"
                  onClick={toggleDropdown}
                >
                  Go to Settings
                </Link>
                <button
                  className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 w-full text-left"
                  role="menuitem"
                  onClick={() => {
                    toggleDropdown(); // Close the dropdown
                    openLanguageSettings(); // Open the modal
                  }}
                >
                  Language Settings
                </button>

                <Link
                  to="/change-password"
                  className="block px-4 py-2 text-sm text-gray-700 hover:bg-gray-100"
                  role="menuitem"
                  onClick={toggleDropdown}
                >
                  Change Password
                </Link>
                <button
                  onClick={handleSignOut}
                  className="block w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-100"
                  role="menuitem"
                >
                  Sign Out
                </button>
              </div>
            </div>
          )}
        </div>
      ) : (
        <Link
          to="/login"
          className="text-blue-500 hover:underline px-3 py-2 inline-block"
        >
          Login
        </Link>
      )}
    </div>
  );
};

export default UserProfile;
