import React, { useState, useCallback, useRef } from 'react';

interface FileUploadResult {
    success: boolean;
    message: string;
}

const LocalStorageSyncPage = () => {
    const [uploadResults, setUploadResults] = useState<FileUploadResult[]>([]);
    const [isProcessing, setIsProcessing] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const fileInputRef = useRef<HTMLInputElement>(null); // Ref for file input

    const handleFileUpload = useCallback(async (
        files: FileList | null,
    ): Promise<void> => {
        if (!files || files.length === 0) {
            setUploadResults(prev => [...prev, {
                success: false,
                message: `No files selected.`
            }]);
            return;
        }

        setIsProcessing(true);
        const newUploadResults: FileUploadResult[] = [];

        // Wrap the file processing in a Promise.all to handle async operations correctly
        await Promise.all(
            Array.from(files).map(async (file) => { // Iterate using Array.from
                if (file.type !== 'application/json') {
                    newUploadResults.push({
                        success: false,
                        message: `File "${file.name}" is not a JSON file.  Skipped.`
                    });
                    return; // Important: Return here to stop processing this file
                }

                const reader = new FileReader();

                try {
                    const content = await new Promise<string>((resolve, reject) => {
                        reader.onload = () => resolve(reader.result as string);
                        reader.onerror = () => reject(reader.error);
                        reader.readAsText(file);
                    });

                    const jsonData = JSON.parse(content);
                    // Use filename (without extension) as the key
                    const fileKey = file.name.replace(/\.json$/, '');
                    localStorage.setItem(fileKey, JSON.stringify(jsonData));
                    newUploadResults.push({
                        success: true,
                        message: `Successfully uploaded and stored "${file.name}" with key "${fileKey}".`
                    });

                } catch (e: any) {
                    newUploadResults.push({
                        success: false,
                        message: `Error processing "${file.name}": ${e.message || 'Unknown error'}`
                    });
                }
            })
        );

        setUploadResults(prevResults => [...prevResults, ...newUploadResults]); // Accumulate results
        setIsProcessing(false);
    }, []);



    const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const files = event.target.files;
        if (files) {
            handleFileUpload(files).catch(e => setError(e.message));
        }
        // Reset the input so you can upload the same files again
        if (fileInputRef.current) {
            fileInputRef.current.value = '';
        }

    };

    return (
        <div className="p-4 space-y-6">
            <h1 className="text-2xl font-bold">Local Storage Sync Page</h1>

            <p className="text-gray-600">
                Upload JSON files to synchronize application data in Local Storage.  This page uses the filename (without extension) as the key for each entry.
            </p>
            {error && (
                <div className="alert alert-error">
                    <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v2m0 4h.01m-6.938-9.47a6 6 0 1113.876 0M12 3v1m0 16v1m-9.38 4.77a8 8 0 01-9.54-9.534m9.38-4.77v-1m3.418 5.748a2 2 0 01-2.179-2.18m0 5.668a2 2 0 012.179-2.176" />
                    </svg>
                    <h3 className="font-bold">Error</h3>
                    <div className="text-xs">
                        {error}
                    </div>
                </div>
            )}

            <div className="space-y-4">
                <h2 className="text-lg font-semibold">JSON File Upload</h2>
                <input
                    type="file"
                    accept=".json"
                    onChange={handleFileChange}
                    disabled={isProcessing}
                    aria-label="Upload JSON Files"
                    multiple
                    ref={fileInputRef} // Add ref here
                />
                <p className="text-sm text-gray-500">
                    Upload one or more JSON files. The filename (without the .json extension) will be used as the key in Local Storage.
                </p>
            </div>

            {uploadResults.length > 0 && (
                <div className="mt-6">
                    <h3 className="text-md font-semibold">Upload Results:</h3>
                    <ul className="mt-2 space-y-2">
                        {uploadResults.map((result, index) => (
                            <li key={index} className={result.success ? "text-green-600" : "text-red-600"}>
                                {result.message}
                            </li>
                        ))}
                    </ul>
                </div>
            )}
            {isProcessing && <p>Processing...</p>}
        </div>
    );
};

export default LocalStorageSyncPage;
