import React, { useEffect, useMemo, useState } from "react";

import { useParams, useNavigate } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import { SongIndicesItem } from "../../interfaces/book"; // Import SongIndicesItem
import { GOOGLE_WEBAPP_URL } from "../../utils/constants";

import { useOfflineMode } from "../../contexts/OfflineModeContext";
//import useDownloadBookSongs from '../../hooks/useDownloadBookSongs';
import { useDownloadStatus } from "../../contexts/DownloadStatusContext";
import { useLanguagePreference } from "../../contexts/LanguagePreferenceContext";
import { transliterateText} from '../../utils/transliterationUtils';

// Declare the Sanscript object in the global scope for TypeScript

declare global {
  interface Window {
    Sanscript: any; // Use 'any' initially for flexibility
  }
}
const LOCAL_STORAGE_KEY_SONG_INDICES = "sankeertan_song_indices";
//const CACHE_EXPIRATION_TIME = 24 * 60 * 60 * 1000; //24 hours



const fetchSongIndices = async (
  bookId?: string
): Promise<SongIndicesItem[]> => {
  if (!bookId) return [];
  const key = `sankeertan_song_indices-${bookId}`;
  const storedIndices = localStorage.getItem(key);
  if (storedIndices) {
    try {
      return JSON.parse(storedIndices) as SongIndicesItem[];
    } catch (error) {
      console.error("Error parsing song indices from localStorage:", error);
    }
  }
  const response = await fetch(
    `${GOOGLE_WEBAPP_URL}?mode=api&apiname=getsongsforbook&book=${bookId}`
  );
  if (!response.ok) {
    const errorData = await response.json();
    throw new Error(
      `Failed to fetch song indices for book ${bookId}: ${
        response.status
      } - ${JSON.stringify(errorData)}`
    );
  }
  const data = await response.json();

  localStorage.setItem(key, JSON.stringify(data));
  return data;
};

function SongIndexList() {
  const { bookId } = useParams<{ bookId: string }>();
  const navigate = useNavigate(); // Initialize useNavigate
  const { isEffectivelyOffline } = useOfflineMode();
  const isSongDownloaded = (songTitle: string) => {
    const key = `sankeertan_song_details-${bookId}-${encodeURIComponent(
      songTitle
    )}`;
    return localStorage.getItem(key) !== null;
  };

  const { preferredLanguage } = useLanguagePreference();
  const [, setRenderTrigger] = useState({}); // State to force re-render
  //console.log("SongIndexList - preferredLanguage:", preferredLanguage);
  useEffect(() => {
    //console.log("LanguagePreferenceContext - preferredLanguage updated:", preferredLanguage);
    // Here you might also save to localStorage or elsewhere
    setRenderTrigger({});
  }, [preferredLanguage]);

  const initialData = useMemo(() => {
    try {
      const storedData = localStorage.getItem(
        `${LOCAL_STORAGE_KEY_SONG_INDICES}-${bookId}`
      );
      if (storedData) {
        //console.log(`initialData::storedData:${storedData}`)
        const parsedData = JSON.parse(storedData);
        if (Array.isArray(parsedData?.data)) {
          return parsedData.data as SongIndicesItem[];
        }
      }
    } catch (error) {
      console.error(
        `Error retrieving song indices for book ${bookId} from localStorage:`,
        error
      ); // Corrected syntax
    }
    return undefined;
  }, [bookId]);

  const {
    isLoading,
    error,
    data: songIndices,
    isFetching,
  } = useQuery<SongIndicesItem[]>({
    queryKey: ["songIndices", bookId, preferredLanguage],
    queryFn: () => fetchSongIndices(bookId!),
    staleTime: Infinity,
    initialData: initialData,
    refetchOnMount: false,
  });

  const { downloadStatus } = useDownloadStatus();



  useEffect(() => {
    if (bookId && songIndices && !isFetching) {
      try {
        const dataToStore = {
          data: songIndices,
          timestamp: Date.now(),
        };
        localStorage.setItem(
          `${LOCAL_STORAGE_KEY_SONG_INDICES}-${encodeURIComponent(bookId)}`,
          JSON.stringify(dataToStore)
        );
      } catch (error) {
        console.error(
          `Error saving song indices for book ${bookId} to localStorage:`,
          error
        );
      }
    }
  }, [bookId, songIndices, isFetching]);

  if (isLoading) {
    return <div>Loading song indices...</div>;
  }

  if (error instanceof Error) {
    return <div>Error loading song indices: {error.message}</div>;
  }

  const handleSongIndexClick = (
    bookId: string | undefined,
    sourceRow: number,
    songTitle: string
  ) => {
    if (bookId) {
      navigate(
        `/details/book/${bookId}/${sourceRow}/${encodeURIComponent(songTitle)}`
      );
    }
  };

  const filteredSongIndices =
    songIndices?.filter((index) => {
      if (!isEffectivelyOffline) {
        return true; // Show all in online mode
      }
      return isSongDownloaded(index.songTitle); // Show only downloaded in offline mode
    }) ?? [];

  return (
    <div>
      {/*<button onClick={openLanguageSettings}>
        Open Language Settings
      </button>

      <LanguageSettingsModal
        isOpen={isLanguageSettingsOpen}
        onClose={closeLanguageSettings}
      />*/}
      <h2>
        Song Indices for Book: {bookId}
      </h2>
      <ul>
        {filteredSongIndices?.map((index) => (
          <li
            key={index.sourceRow}
            onClick={() =>
              handleSongIndexClick(bookId, index.sourceRow, index.songTitle)
            }
            style={{
              cursor: "pointer",
              color: localStorage.getItem(
                `sankeertan_song_details-${bookId}-${encodeURIComponent(
                  index.songTitle
                )}`
              )
                ? "green"
                : "black",
            }}
          >
            <span style={{ flexGrow: 1 }}>
              {preferredLanguage === "itrans"
                ? index.songTitle
                : window.Sanscript
                ? transliterateText(index.songTitle, 'itrans', preferredLanguage) 
                : index.songTitle}
            </span>
            <span style={{ marginLeft: "10px", color: "red" }}>
              {bookId &&
                downloadStatus[bookId]?.downloadingSongs[index.songTitle] &&
                `..downloading..`}
            </span>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default SongIndexList;
