import { useState, useCallback } from 'react';
import { useDownloadStatus } from '../contexts/DownloadStatusContext';
import { GOOGLE_WEBAPP_URL } from '../utils/constants';

interface StoredSongIndices {
  data: { sourceRow: number; songTitle: string; verseCount?: number }[];
  timestamp?: number; // Assuming a timestamp might be present
}

const fetchSongDetails = async ({ bookId, sourceRow, songTitle }: { bookId: string; sourceRow: number; songTitle: string }): Promise<any[]> => {
  if (!bookId || !sourceRow || !songTitle) {
    throw new Error('Missing parameters to fetch song details');
  }
  const response = await fetch(`${GOOGLE_WEBAPP_URL}?mode=api&apiname=getsongtextforsong&book=${bookId}&sourcerow=${sourceRow}&songtitle=${songTitle}`);
  if (!response.ok) {
    const errorData = await response.json();
    throw new Error(`Failed to fetch song details for book ${bookId}, row ${sourceRow}, title ${songTitle}: ${response.status} - ${JSON.stringify(errorData)}`);
  }
  return await response.json();
};

const DOWNLOAD_DELAY_MS = 3000; // Adjust this value (in milliseconds) to control the delay
const DOWNLOAD_LIMIT_PER_CLICK = 10; // Adjust this value to set the number of songs to download per click


interface UseDownloadBookSongsResult {
  isDownloading: boolean;
  downloadAllSongsForBook: (bookId: string) => Promise<number>;
  isDownloadingBook: (bookId: string) => boolean
  getDownloadedSongCount: (bookId: string) => number; // New method;
}

const useDownloadBookSongs = (
  onDownloadStart?: () => void,
  onDownloadComplete?: () => void,
  onError?: (error: string) => void
): UseDownloadBookSongsResult => {
  const [isDownloading, setIsDownloading] = useState(false);
  const [downloadingBookId, setDownloadingBookId] = useState<string | null>(null);
  const { updateDownloadProgress, markSongAsDownloading, markSongAsDownloaded } = useDownloadStatus();

  const isCurrentlyDownloadingBook = useCallback((bookId: string) => {
    return downloadingBookId === bookId && isDownloading;
  }, [downloadingBookId, isDownloading]);

  const getDownloadedSongCount = useCallback((bookId: string): number => {
    let downloadedCount = 0;
    const localStorageKey = `sankeertan_song_indices-${bookId}`;
    const storedIndices = localStorage.getItem(localStorageKey);

    if (storedIndices) {
      try {
        const parsedIndices = JSON.parse(storedIndices) as StoredSongIndices;
        if (parsedIndices?.data && Array.isArray(parsedIndices.data)) {
          parsedIndices.data.forEach(song => {
            const songDetailsKey = `sankeertan_song_details-${bookId}-${encodeURIComponent(song.songTitle)}`;
            if (localStorage.getItem(songDetailsKey)) {
              downloadedCount++;
            }
          });
        }
      } catch (error) {
        console.error('Error parsing song indices from localStorage:', error);
      }
    }
    return downloadedCount;
  }, []);

  const downloadAllSongsForBook = useCallback(async (bookId: string): Promise<number> => {
    if (!bookId || (isDownloading && downloadingBookId === bookId)) {
      return 0;
    }

    setDownloadingBookId(bookId);
    setIsDownloading(true);
    onDownloadStart?.();
 
    const localStorageKey = `sankeertan_song_indices-${bookId}`;
    const storedIndices = localStorage.getItem(localStorageKey);
    let songIndices: { sourceRow: number; songTitle: string }[] | null = null;
    let totalCount = 0;

    if (storedIndices) {
      try {
        const parsedIndices = JSON.parse(storedIndices) as StoredSongIndices;
        if (parsedIndices?.data && Array.isArray(parsedIndices.data)) {
          songIndices = parsedIndices.data.map(item => ({
            sourceRow: item.sourceRow,
            songTitle: item.songTitle,
            verseCount: item.verseCount,
          }));
          totalCount=songIndices.length;
          updateDownloadProgress(bookId, totalCount); // Initialize total count in context
        } else {
          console.warn(`Stored song indices for book ${bookId} is not a valid array.`);
        }
      } catch (error) {
        console.error('Error parsing song indices from localStorage:', error);
      }
    }

    // Fetch from server ONLY if not found in localStorage
    if (!songIndices) {
      try {
        const response = await fetch(`${GOOGLE_WEBAPP_URL}?mode=api&apiname=getsongsforbook&book=${bookId}`);
        if (!response.ok) {
          const errorData = await response.json();
          throw new Error(`Failed to fetch song indices for book ${bookId}: ${response.status} - ${JSON.stringify(errorData)}`);
        }
        const res = await response.json() as { sourceRow: number; songTitle: string }[];
        songIndices = res;
        totalCount = songIndices.length;
        updateDownloadProgress(bookId, totalCount);
        localStorage.setItem(localStorageKey, JSON.stringify({ data: res, timestamp: Date.now() })); // Store in the correct format
      } catch (error: any) {
        onError?.(`Error fetching song indices: ${error.message}`);
        return 0;
      }
    }

    if (songIndices && songIndices.length > 0) {
      let downloadedInThisClick = 0;
      for (const index of songIndices) {
        const key = `sankeertan_song_details-${bookId}-${encodeURIComponent(index.songTitle)}`;
        if (!localStorage.getItem(key)) {
          markSongAsDownloading(bookId, index.songTitle);
          try {
            const res=await fetchSongDetails({ bookId, sourceRow: index.sourceRow, songTitle: index.songTitle });
            localStorage.setItem(key, JSON.stringify({ data: res, timestamp: Date.now() }));
            markSongAsDownloaded(bookId, index.songTitle);
            downloadedInThisClick++;
          } catch (error: any) {
            onError?.(`Error downloading ${index.songTitle}: ${error.message}`);
          }
          await new Promise(resolve => setTimeout(resolve, DOWNLOAD_DELAY_MS));
          if (downloadedInThisClick >= DOWNLOAD_LIMIT_PER_CLICK) break;
        } else {
          markSongAsDownloaded(bookId, index.songTitle); 
        }
      }
    }
    onDownloadComplete?.();
    setIsDownloading(false);
    setDownloadingBookId(null);
    return totalCount;
  }, [onDownloadStart, onDownloadComplete, onError, isDownloading, downloadingBookId, updateDownloadProgress, markSongAsDownloading, markSongAsDownloaded]);


  return {
    isDownloading,
    downloadAllSongsForBook,
    getDownloadedSongCount,
    isDownloadingBook: isCurrentlyDownloadingBook };
};

export default useDownloadBookSongs;