import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { BrowserRouter } from 'react-router-dom'; 
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { LanguagePreferenceProvider } from './contexts/LanguagePreferenceContext';
const queryClient = new QueryClient({
  defaultOptions: { // Correct property name
    queries: {
      staleTime: Infinity, // Set staleTime globally to a large value
      gcTime: Infinity,    // Set garbage collection time to Infinity
    },
  },
});
const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <QueryClientProvider client={queryClient}>
     <BrowserRouter>
     <LanguagePreferenceProvider>
    <App />
    </LanguagePreferenceProvider>
    </BrowserRouter>
    </QueryClientProvider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
