import React, { useEffect, useState, useRef } from 'react';
import { supabase } from '../../../utils/supabase';
import { RealtimePostgresChangesPayload,RealtimeChannel } from '@supabase/supabase-js';

interface SankeertanRow {
  id: number; 
  content: string | null;
  scroll_position: number | null;
}

interface SankeertanListenerProps {
  broadcastId: number;
  session: any; // The Supabase session object
  shouldSyncContent: boolean;
  shouldSyncScroll: boolean;
  onContentUpdated: (content: string | null) => void;
  onScrollPositionUpdated: (rowId: number | null) => void;
}

const SankeertanListener: React.FC<SankeertanListenerProps> = ({
  broadcastId,
  session,
  shouldSyncContent,
  shouldSyncScroll,
  onContentUpdated,
  onScrollPositionUpdated,
}) => {
  const channel = useRef<RealtimeChannel | null>(null); 

  useEffect(() => {
    if (!session?.access_token || !broadcastId) {
      console.log('No access token or broadcast ID, not subscribing to sankeertan updates.');
      if (channel.current) {
        channel.current.unsubscribe();
        channel.current = null;
      }
      return;
    }

    channel.current = supabase.channel(`broadcast:${broadcastId}`);

    channel.current
      .on(
        'postgres_changes',
        {
          event: 'UPDATE',
          schema: 'public',
          table: 'sankeertan',
          filter: `id=eq.${broadcastId}`,
        },
        (payload: RealtimePostgresChangesPayload<SankeertanRow>) => {
          if (payload.new && 'content' in payload.new && shouldSyncContent) {
            onContentUpdated(payload.new.content);
          }
          if (
            payload.new &&
            'scroll_position' in payload.new &&
            payload.new.scroll_position !== null &&
            shouldSyncScroll
          ) {
            onScrollPositionUpdated(payload.new.scroll_position);
          }
        }
      )
      .subscribe((status) => {
        if (status === 'SUBSCRIBED') {
          console.log(`Successfully subscribed to sankeertan updates for broadcast: ${broadcastId}`);
        } else if (status === 'CHANNEL_ERROR') { 
          console.error(
            `Error subscribing to sankeertan updates for broadcast ${broadcastId}:`,
            status
          );
        } else if (status === 'TIMED_OUT') {
          console.warn(`Subscription timed out for broadcast ${broadcastId}:`, status);
        } else if (status === 'CLOSED') {
          console.warn(`Subscription closed for broadcast ${broadcastId}:`, status);
        }
      });

    // Unsubscribe when the component unmounts or dependencies change
    return () => {
      if (channel.current) {
        channel.current.unsubscribe();
        channel.current = null;
      }
    };
  }, [broadcastId, session?.access_token, shouldSyncContent, shouldSyncScroll, onContentUpdated, onScrollPositionUpdated]);

  return null; // This component doesn't render anything directly, it just listens for events
};

export default SankeertanListener;