// LanguageSettingsModal.tsx
import React, { useState } from "react";
import { Dialog, Transition } from "@headlessui/react";
import { Fragment } from "react";


interface LanguageSettingsModalProps {
  isOpen: boolean;
  onClose: () => void;
  onLanguageSave: (language: string) => void; 
  currentLanguage: string;
}

const LanguageSettingsModal: React.FC<LanguageSettingsModalProps> = ({
  isOpen,
  onClose,
  onLanguageSave,
  currentLanguage, 
}) => {
  //console.log("LanguageSettingsModal - isOpen prop:", isOpen);
  const [selectedLanguage, setSelectedLanguage] = useState(currentLanguage); // Initialize with the context value

  const handleLanguageChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSelectedLanguage(event.target.value);
  };

  const handleSave = () => {
    //console.log("handleSave called. selectedLanguage:", selectedLanguage);
    //setPreferredLanguage(selectedLanguage); // Update the context
    onLanguageSave(selectedLanguage);
    //console.log("setPreferredLanguage called with:", selectedLanguage);
    onClose(); // Close the modal
  };

  return (
    <Transition appear show={isOpen} as={Fragment}>
      <Dialog as="div" className="relative z-10" onClose={onClose}>
        <Transition.Child
          as="div"
          enter="ease-out duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="ease-in duration-200"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-black bg-opacity-25" />{" "}
          {/* The overlay */}
        </Transition.Child>

        <div className="fixed inset-0 overflow-y-auto">
          <div className="flex min-h-full items-center justify-center p-4 text-center">
            <Transition.Child
              as="div"
              enter="ease-out duration-300"
              enterFrom="opacity-0 scale-95"
              enterTo="opacity-100 scale-100"
              leave="ease-in duration-200"
              leaveFrom="opacity-100 scale-100"
              leaveTo="opacity-0 scale-95"
            >
              <Dialog.Panel className="w-full max-w-md transform overflow-hidden rounded-2xl bg-white p-6 text-left align-middle shadow-xl transition-all">
                <Dialog.Title
                  as="h3"
                  className="text-lg font-medium leading-6 text-gray-900"
                >
                  Language Settings
                </Dialog.Title>
                <div className="mt-2">
                  <label className="block">
                    <input
                      type="radio"
                      name="language"
                      value="iast"
                      checked={selectedLanguage === "iast"}
                      onChange={handleLanguageChange}
                      className="mr-2" // Add some spacing
                    />
                    IAST
                  </label>
                  <br />
                  <label className="block">
                    <input
                      type="radio"
                      name="language"
                      value="tamil_superscripted"
                      checked={selectedLanguage === "tamil_superscripted"}
                      onChange={handleLanguageChange}
                      className="mr-2" // Add some spacing
                    />
                    Tamil
                  </label>
                  <br />
                  {/* Add more language options here following the same pattern */}
                  <label className="block">
                    <input
                      type="radio"
                      name="language"
                      value="malayalam"
                      checked={selectedLanguage === "malayalam"}
                      onChange={handleLanguageChange}
                      className="mr-2" // Add some spacing
                    />
                    Malayalam
                  </label>
                  <br />
                  {/* Example for another language */}
                  <label className="block">
                    <input
                      type="radio"
                      name="language"
                      value="devanagari"
                      checked={selectedLanguage === "devanagari"}
                      onChange={handleLanguageChange}
                      className="mr-2" // Add some spacing
                    />
                    Devanagari
                  </label>
                  <br />
                  <label className="block">
                    <input
                      type="radio"
                      name="language"
                      value="itrans"
                      checked={selectedLanguage === "itrans"}
                      onChange={handleLanguageChange}
                      className="mr-2" // Add some spacing
                    />
                    ITrans
                  </label>
                  <br />
                </div>
                <div className="mt-4">
                  <button
                    type="button"
                    className="inline-flex justify-center rounded-md border border-transparent bg-gray-200 px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-300 focus:outline-none focus-visible:ring-2 focus-visible:ring-gray-500 focus-visible:ring-offset-2 mr-2" // Added mr-2
                    onClick={onClose}
                  >
                    Close
                  </button>
                  <button
                    type="button"
                    className="inline-flex justify-center rounded-md border border-transparent bg-blue-100 px-4 py-2 text-sm font-medium text-blue-900 hover:bg-blue-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-blue-500 focus-visible:ring-offset-2"
                    onClick={handleSave}
                  >
                    Save
                  </button>
                </div>
              </Dialog.Panel>
            </Transition.Child>
          </div>
        </div>
      </Dialog>
    </Transition>
  );
};

export default LanguageSettingsModal;
