import React, { useState, useRef, useEffect, useCallback } from 'react';
//import SankeertanListener from './SankeertanListener';
import SankeertanListener from './SankeertanListener';
import { supabase } from '../../../utils/supabase';
import { Session } from '@supabase/supabase-js';
import { transliterateText } from '../../../utils/transliterationUtils';
import { useLanguagePreference } from '../../../contexts/LanguagePreferenceContext';

interface BroadcastRow {
  id: number;
  content: string | null; // Adjust the type of 'content'
  scroll_position: number | null;
}

const SankeertanDisplay = ({ broadcastId }: { broadcastId: number }) => {
  const [broadcastedContent, setBroadcastedContent] = useState<string | null>(null);
  const [scrollPosition, setScrollPosition] = useState<string | null>(null);
  const contentRef = useRef<HTMLDivElement>(null);
  const [session, setSession] = useState<Session | null>(null); // Explicitly type session state
  
  const [shouldSyncContent, setShouldSyncContent] = useState(true);
  const [shouldSyncScroll, setShouldSyncScroll] = useState(true);
  const [loadingInitialData, setLoadingInitialData] = useState(true);
  const [initialDataError, setInitialDataError] = useState<string | null>(null);  

  const { preferredLanguage } = useLanguagePreference();

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session);
    });

    supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session);
    });
  }, []);

  /*
  async function fetchInitialData() {
    setLoadingInitialData(true);
    setInitialDataError(null);
    try {
      const { data, error } = await supabase
        .from("sankeertan")
        .select("content, scroll_position")
        .eq("id", broadcastId)
        .single<Pick<BroadcastRow, "content" | "scroll_position">>();

      if (error) {
        console.error("Error fetching broadcast data:", error);
        setInitialDataError(error.message);
      } else if (data) {
        const safeContent = data.content ?? ""; 
        let content = transliterateText(safeContent, 'devanagari', preferredLanguage);
        setBroadcastedContent(content);
        //setScrollPosition(data.scroll_position);
      }
    } finally {
      setLoadingInitialData(false);
    }
  }
*/
const fetchInitialData = useCallback(async () => {
  setLoadingInitialData(true);
  setInitialDataError(null);
  try {
    const { data, error } = await supabase
      .from("sankeertan")
      .select("content, scroll_position")
      .eq("id", broadcastId)
      .single<Pick<BroadcastRow, "content" | "scroll_position">>();

    if (error) {
      console.error("Error fetching broadcast data:", error);
      setInitialDataError(error.message);
    } else if (data) {
      const safeContent = data.content ?? "";
      let content = transliterateText(safeContent, 'devanagari', preferredLanguage);
      setBroadcastedContent(content);
      // setScrollPosition(data.scroll_position);
    }
  } finally {
    setLoadingInitialData(false);
  }
}, [broadcastId, preferredLanguage]); 

useEffect(() => {
  if (session && broadcastId) {
    fetchInitialData();
  }
}, [session, broadcastId, fetchInitialData]);


  useEffect(() => {
    if (scrollPosition && contentRef.current) {
      const targetElement = contentRef.current.querySelector(
        `#trRowAsAnchor-${scrollPosition}`
      );
      if (targetElement) {
        targetElement.scrollIntoView({ block: 'start' });
      }
      // Reset scrollPosition after scrolling
      setScrollPosition(null);
    }
  }, [scrollPosition, contentRef]);

  const handleContentUpdated = (content: string | null) => {
    const safeContent = content ?? ""; 
    content = transliterateText(safeContent, 'devanagari', preferredLanguage);
    setBroadcastedContent(content);
  };

  const handleScrollPositionUpdated = (rowId: number | null) => {
    setScrollPosition(`#trRowAsAnchor-${rowId}`);
  };

  return (
    <div>
      <h2>Sankeertan Content</h2>
      <div ref={contentRef}>
        {broadcastedContent && <div dangerouslySetInnerHTML={{ __html: broadcastedContent }} />}
      </div>

      {session && (
        <SankeertanListener
          broadcastId={broadcastId}
          session={session}
          shouldSyncContent={shouldSyncContent}
          shouldSyncScroll={shouldSyncScroll}
          onContentUpdated={handleContentUpdated}
          onScrollPositionUpdated={handleScrollPositionUpdated}
        />
      )}
    </div>
  );
};

export default SankeertanDisplay;