import React, { useEffect, useMemo, useState ,useRef, useCallback} from 'react';
import { useParams } from 'react-router-dom';
import { useQuery } from '@tanstack/react-query';
//import { SongDetailsItem } from '../../interfaces/book' ;
import {GOOGLE_WEBAPP_URL} from '../../utils/constants';
import JsonToHtmlTable from './JsonToHtmlTable';
import { useLanguagePreference } from '../../contexts/LanguagePreferenceContext';
import { useGesture  } from '@use-gesture/react';
import { motion, AnimatePresence } from 'framer-motion'; 
import {supabase} from '../../utils/supabase';

const LOCAL_STORAGE_KEY_SONG_DETAILS = 'sankeertan_song_details';
//const CACHE_EXPIRATION_TIME = 24 * 60 * 60 * 1000;

const broadcastSong = (songTitle: string) => {
  console.log(`Broadcasting song: ${songTitle}`);
  // Your actual broadcast logic here (e.g., sending data to a server, updating UI)
  alert(`Broadcasting ${songTitle}! (Simulated)`); // Simple feedback for demonstration
};

export const broadcastContent = async function (content: string) {
  try{
    //const client=getsbclient()
    await supabase
      .from("sankeertan")
      .upsert({ id: 1, content: content, scroll_position: 0 });
    return true;
  }
  catch(e){
    console.error(e)
    return false;
  }
};


const fetchSongDetails = async ({ bookId, sourceRow, songTitle }: { bookId: string; sourceRow: string; songTitle: string }): Promise<any[]> => {
  if (!bookId || !sourceRow) {
    throw new Error('bookId or sourceRow is not defined. Cannot fetch songDetails');
  }
  const response = await fetch(`${GOOGLE_WEBAPP_URL}?mode=api&apiname=getsongtextforsong&book=${bookId}&sourcerow=${sourceRow}&songtitle=${songTitle}`);
  if (!response.ok) {
    const errorData = await response.json();
    throw new Error(`Failed to fetch song details for book ${bookId}+row ${sourceRow}: ${response.status} - ${JSON.stringify(errorData)}`);
  }
  return await response.json();
};

function SongDetailView() {
  const { bookId, sourceRow, songTitle } = useParams<{ bookId: string; sourceRow: string; songTitle: string }>();
  const queryKey = ['songDetails', bookId, sourceRow];
  const encodedSongTitle = songTitle ? encodeURIComponent(songTitle) : '';
  const songDetailsKey = `${LOCAL_STORAGE_KEY_SONG_DETAILS}-${bookId}-${encodedSongTitle}`;
  const { preferredLanguage } = useLanguagePreference();
  const initialData = useMemo(() => {
    try {
      const storedData = localStorage.getItem(songDetailsKey);
      if (storedData) {
        const parsedData = JSON.parse(storedData);
          return parsedData.data as any[];
      }
    } catch (error) {
      console.error(`Error retrieving song details for key ${songDetailsKey} from localStorage:`, error);
    }
    return undefined;
  }, [songDetailsKey]);

  const { isLoading, error, data: songDetails   } = useQuery<any[]>({
    queryKey: queryKey,
    queryFn: () => fetchSongDetails({ bookId: bookId!, sourceRow: sourceRow!, songTitle: songTitle! }),
    staleTime: Infinity,
    initialData: initialData,
    refetchOnMount: false,
  });

  const [isLongPressActive, setIsLongPressActive] = useState(false);
  const [showContextMenu, setShowContextMenu] = useState(false);
  const [menuPosition, setMenuPosition] = useState({ x: 0, y: 0 });
  const longPressTimeout = useRef<NodeJS.Timeout | null>(null);
  const menuRef = useRef<HTMLDivElement>(null);

  const longPressHandlers  = useGesture(
    {
      onPointerDown: (state ) => { 
        //console.log(`onPointerDown:${JSON.stringify(state.event.type)}`);
        setIsLongPressActive(state.down);
        if (state.event.type==='pointerdown') {
          //console.log('onPointerDown: Timer started');
          longPressTimeout.current = setTimeout(() => {
            if (longPressTimeout.current) {
              //console.log('onPointerDown: Long press timeout expired');
              setMenuPosition({
                x: state.event.clientX,
                y: state.event.clientY,
              });
              setShowContextMenu(true);
              setIsLongPressActive(false);
            }
          }, 500);
        }
      },
      onPointerUp: (state) => {  
        //console.log('onPointerUp', JSON.stringify(state.event.type)); 
        setIsLongPressActive(false);
        if (longPressTimeout.current) {
          //console.log('onPointerUp: Timer cleared');
          clearTimeout(longPressTimeout.current);
          longPressTimeout.current = null;
        }
      },
      onContextMenu: (state) => { //  Prevent default context menu
        state.event.preventDefault();
      },
      onClick: (state) => {
        //console.log('onClick', JSON.stringify(state.event.type));
        if (state.event ) {
        state.event.stopPropagation(); ;
        }
      },
      onPointerCancel: (state) => {
        //console.log('onPointerCancel', state.event);
        setIsLongPressActive(false);
        if (longPressTimeout.current) {
          //console.log('onPointerCancel: Timer cleared');
          clearTimeout(longPressTimeout.current);
          longPressTimeout.current = null;
        }
      },
   },
    {}
  );

  // Close menu on outside click
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (menuRef.current && !menuRef.current.contains(event.target as Node)) {
        setShowContextMenu(false);
      }
    };
    if (showContextMenu) {
      document.addEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [showContextMenu]);

  const handleMenuOptionClick = (option: string) => {
    //console.log(`Menu option clicked: ${option}`);
    setShowContextMenu(false); // Close menu after option click
    if (option === 'broadcast') {
      if(songTitle){
        handleBroadcast();
      }
    }
    // Handle other options
  };

  const [isBroadcasting, setIsBroadcasting] = useState(false);
  const [broadcastStatus, setBroadcastStatus] = useState<
    "success" | "error" | "broadcasting" | "failed" | null
  >(null);
  const [canBroadcast, setCanBroadcast] = useState(true); // Initially can broadcast

  const broadcastContentRef = useRef<HTMLDivElement>(null); // New ref for the broadcastable content

  const handleBroadcast = useCallback(async () => {
    //console.log("here in handleBroadcast");
    if (isBroadcasting ) {
      console.warn("isBroadcasting already");
      return; // Prevent new broadcast if one is in progress or cannot broadcast
    }
    if (!canBroadcast) {
      alert("Cannot do repeat Broadcast... Pls check if song has changed");
      return; // Prevent new broadcast if one is in progress or cannot broadcast
    }    
    if (broadcastContentRef.current) {
      setIsBroadcasting(true);
      setBroadcastStatus("broadcasting"); // Optional: Visual feedback for 'broadcasting'
      try {
        const htmlToBroadcast = broadcastContentRef.current.innerHTML;
        const result = await broadcastContent(htmlToBroadcast);
        setIsBroadcasting(false);
        setBroadcastStatus("success");
        setCanBroadcast(false); // Disable after successful broadcast
        setTimeout(() => setBroadcastStatus(null), 2000); // Clear success feedback after 2 seconds
      } catch (error: any) {
        console.error("Broadcast failed:", error);
        setIsBroadcasting(false);
        setBroadcastStatus("error");
        setTimeout(() => setBroadcastStatus(null), 3000); // Clear error feedback after 3 seconds
      }
    } else {
      console.warn(
        "broadcastContentRef.current is null. Cannot broadcast content."
      );
      setBroadcastStatus("failed"); // Optional: Update status if ref is invalid
      setIsBroadcasting(false);
    }
  }, [broadcastContentRef, isBroadcasting, canBroadcast]);

  // Enable broadcast when songVerses changes (i.e., new content is loaded)
  useEffect(() => {
    setCanBroadcast(true);
  }, [songDetails]);

  useEffect(() => {
    if (songDetails) {
      try {
        localStorage.setItem(
          songDetailsKey,
          JSON.stringify({ data: songDetails, timestamp: Date.now() })
        );
      } catch (e) {
        console.error('Error saving song details to localStorage:', e);
      }
    }
  }, [songDetailsKey, bookId, sourceRow,songDetails]);
  
  if (isLoading) {
    return <div>Loading song details...</div>;
  }

  if (error) {
    return <div>Error loading song details: {error.message}</div>;
  }
  //console.log("Song Details Data:", JSON.stringify(songDetails));
  return (
    <div {...longPressHandlers()} // Changed to longPressHandlers()
    className={`song-details-container ${isLongPressActive ? 'long-press-active' : ''}`}
    style={{ cursor: 'pointer' }}>
      <h1 className="text-2xl font-bold text-gray-800 mb-2">
        {bookId && songTitle}
      </h1>
      <div ref={broadcastContentRef}>
      {songDetails  &&  (
        <JsonToHtmlTable
          data={songDetails[0]}
          className="my-4"
          highlightColor="bg-blue-50"
          striped={true}
          hoverEffect={true}
          wrapColumns={[1, 2, 3]} // Wrap 3rd and 4th columns
          maxWidth="max-w-6xl"
          preferredLanguage={preferredLanguage}
          sourceLanguage={songDetails[2]}
        />
          )}
        </div>
        <AnimatePresence>
        {showContextMenu && (
          <motion.div
            ref={menuRef}
            initial={{ opacity: 0, scale: 0.95 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0.9 }}
            transition={{ duration: 0.1 }}
            style={{
              position: 'fixed',
              top: menuPosition.y,
              left: menuPosition.x,
              backgroundColor: 'white',
              border: '1px solid #ccc',
              borderRadius: '4px',
              padding: '8px',
              boxShadow: '0 2px 5px rgba(0, 0, 0, 0.2)',
              zIndex: 10,
              minWidth: '150px',
            }}
          >
            <ul style={{ listStyle: 'none', padding: 0, margin: 0 }}>
              <li
                style={{ padding: '6px 12px', cursor: 'pointer' }}
                onClick={() => handleMenuOptionClick('broadcast')}
              >
                Broadcast
              </li>
              <li
                style={{ padding: '6px 12px', cursor: 'pointer' }}
                onClick={() => handleMenuOptionClick('option2')}
              >
                Option 2
              </li>
              {/* Add more options as needed */}
            </ul>
          </motion.div>
        )}
      </AnimatePresence>
  </div>
  );
}

export default SongDetailView;