// contexts/DownloadStatusContext.tsx
import React, { createContext, useState,useCallback, useContext,ReactNode  } from 'react';

interface DownloadStatusProviderProps {
    children?: ReactNode; // '?' makes it optional if needed, but it's usually expected
  }
  
interface DownloadStatus {
  [bookId: string]: {
    totalSongs: number;
    downloadedSongs: number;
    downloadingSongs: { [songTitle: string]: boolean }; // Track individual song download status
  };
}

interface DownloadStatusContextType {
    downloadStatus: DownloadStatus;
    updateDownloadProgress: (bookId: string, totalSongs: number) => void;
    updateDownloadedCount: (bookId: string, downloadedSongs: number) => void; 
    markSongAsDownloading: (bookId: string, songTitle: string) => void;
    markSongAsDownloaded: (bookId: string, songTitle: string) => void;
  }

const DownloadStatusContext = createContext<DownloadStatusContextType | undefined>(undefined);

export const DownloadStatusProvider: React.FC<DownloadStatusProviderProps> = ({ children }) => {
  const [downloadStatus, setDownloadStatus] = useState<DownloadStatus>({});

  const updateDownloadedCount = useCallback((bookId: string, downloadedSongs: number) => {
    setDownloadStatus((prevStatus) => ({
      ...prevStatus,
      [bookId]: {
        ...prevStatus[bookId],
        downloadedSongs,
      },
    }));
  }, []);

 const updateDownloadProgress = useCallback((bookId: string, totalSongs: number) => {
    setDownloadStatus((prevStatus) => ({
      ...prevStatus,
      [bookId]: {
        totalSongs,
        downloadedSongs: prevStatus[bookId]?.downloadedSongs || 0,
        downloadingSongs: prevStatus[bookId]?.downloadingSongs || {},
      },
    }));
  }, []);

  const markSongAsDownloading = (bookId: string, songTitle: string) => {
    setDownloadStatus((prevStatus) => ({
      ...prevStatus,
      [bookId]: {
        ...prevStatus[bookId],
        downloadingSongs: {
          ...prevStatus[bookId]?.downloadingSongs,
          [songTitle]: true,
        },
      },
    }));
  };

  const markSongAsDownloaded = (bookId: string, songTitle: string) => {
    setDownloadStatus((prevStatus) => {
      const updatedDownloadingSongs = { ...prevStatus[bookId]?.downloadingSongs };
      delete updatedDownloadingSongs[songTitle];
      return {
        ...prevStatus,
        [bookId]: {
          ...prevStatus[bookId],
          downloadedSongs: (prevStatus[bookId]?.downloadedSongs || 0) + 1,
          downloadingSongs: updatedDownloadingSongs,
        },
      };
    });
  };

  return (
<DownloadStatusContext.Provider value={{ downloadStatus, updateDownloadProgress, updateDownloadedCount, markSongAsDownloading, markSongAsDownloaded }}>
  {children}
</DownloadStatusContext.Provider>
  );
};

export const useDownloadStatus = () => {
  const context = useContext(DownloadStatusContext);
  if (!context) {
    throw new Error("useDownloadStatus must be used within a DownloadStatusProvider");
  }
  return context;
};