import React,{useEffect,useMemo} from 'react';
import { useCategory } from '../../contexts/CategoryContext';
import { useParams,useNavigate } from 'react-router-dom';
import { useOfflineMode } from '../../contexts/OfflineModeContext';
import useDownloadBookSongs from '../../hooks/useDownloadBookSongs';
import { useDownloadStatus } from '../../contexts/DownloadStatusContext';


function BookList( ) {
  const { categoryId } = useParams<{ categoryId: string }>();
  const { categories, isLoading, error } = useCategory();
  const navigate = useNavigate();
  const { isEffectivelyOffline } = useOfflineMode();
  const { isDownloading:isGlobalDownloading, downloadAllSongsForBook,getDownloadedSongCount,isDownloadingBook } = useDownloadBookSongs();
  const { downloadStatus,updateDownloadProgress,updateDownloadedCount } = useDownloadStatus();
  //const [downloadingBookId, setDownloadingBookId] = useState<string | null>(null);
  //const [downloadedCounts, setDownloadedCounts] = useState<{ [bookId: string]: number }>({});
  const selectedCategory = categories?.find(cat => cat.id === categoryId);
  //const filteredBooks = isEffectivelyOffline ?  selectedCategory?.books?.filter(book => /* your offline filter logic */ true) : books; // Assuming you have this
  


  const filteredBooks = useMemo(() => {
    return selectedCategory?.books?.filter(book => {
      if (!isEffectivelyOffline) {
        return true;
      }
      return localStorage.getItem(`sankeertan_song_indices-${encodeURIComponent(book.bookId)}`);
    }) ?? []; // Provide a default empty array in case selectedCategory?.books is undefined
  }, [selectedCategory?.books, isEffectivelyOffline]);

  useEffect(() => {
    if (filteredBooks) {
      filteredBooks.forEach(book => {
        //const downloadedCount = getDownloadedSongCount(book.bookId); // Use the new method
        if (book.songCount) {
          updateDownloadProgress(book.bookId, book.songCount); // Initialize total count
          const downloadedCount = getDownloadedSongCount(book.bookId); // Get downloaded count
          updateDownloadedCount(book.bookId, downloadedCount); // Update downloaded count in context
        }
      });
    }
  }, [filteredBooks, getDownloadedSongCount, updateDownloadProgress, updateDownloadedCount]); 

  useEffect(() => {
    const initialCounts: { [bookId: string]: number } = {};
              
    filteredBooks?.forEach(book => {
      const keyPrefix = `sankeertan_song_indices-${encodeURIComponent(book.bookId)}`;
      const storedIndices = localStorage.getItem(keyPrefix);
                                              
      if (storedIndices) {
        try {
          const parsedData = JSON.parse(storedIndices);
          if (parsedData && Array.isArray(parsedData.data)) { // Access the 'data' property
            const indices: { songTitle: string; sourceRow: number }[] = parsedData.data;
            let downloaded = 0;
            indices.forEach(index => {
              const songKey = `sankeertan_song_details-${book.bookId}-${encodeURIComponent(index.songTitle)}`;
              if (localStorage.getItem(songKey)) {
                downloaded++;
              }
            });
            initialCounts[book.bookId] = downloaded;
          } else {
            console.warn(`Song indices for book ${book.bookId} are not in the expected format:`, parsedData);
            initialCounts[book.bookId] = 0;
          }
        } catch (error) {
          console.error(`Error parsing song indices for book ${book.bookId}:`, error);
        }
      } else {
        initialCounts[book.bookId] = 0;
      }
    });
  }, [filteredBooks]);
  if (isLoading) {
    return <div>Loading books...</div>;
  }

  if (error instanceof Error) {
    return <div>Error loading books: {error.message}</div>;
  }

  
  const handleBookClick = (bookId: string) => {
    // Navigate to the book details page using the bookId
    navigate(`/book/${bookId}`);
  };

  const handleDownloadClick = async (bookId: string, totalSongs: number) => {
    //localStorage.setItem(`downloading-${encodeURIComponent(bookId)}`, 'true');
    await downloadAllSongsForBook(bookId); 
    //localStorage.removeItem(`downloading-${encodeURIComponent(bookId)}`);
  };


  return (
    <div>
      <h2>{selectedCategory?.name || 'Books'}</h2>
      {filteredBooks ? (
        <ul>
          {filteredBooks.map((book) => (
            <li key={book.bookId}
            onClick={() => handleBookClick(book.bookId)} 
            style={{ cursor: 'pointer', color: localStorage.getItem(`sankeertan_song_indices-${encodeURIComponent(book.bookId)}`) ? "green" : "black"  }} 
            className="grid grid-cols-3 gap-2"
            >
            <div className="flex-grow">
              {book.bookName}
            </div>
            <div>
              ({downloadStatus[book.bookId]?.downloadedSongs || 0}/{book.songCount || 'N/A'} downloaded)
            </div>
            <div> 
              {getDownloadedSongCount(book.bookId) < (book.songCount || 0) ? (
                <button
                  onClick={() => handleDownloadClick(book.bookId, book.songCount ?? 0)}
                  disabled={isEffectivelyOffline || isGlobalDownloading || isDownloadingBook(book.bookId)}
                  className="flex items-center"
                >
                  {isDownloadingBook(book.bookId) ? (
                    'Downloading...'
                  ) : (
                    <>
                     Download All
                    </>
                  )}
                </button>
              ) : (
                <span className="text-green-500">All Downloaded</span>
              )}
              {isEffectivelyOffline && <span className="ml-2"> (Offline)</span>}
            </div>
            </li>
          ))}
        </ul>
      ) : (
        <p>No books found in this category.</p>
      )}
    </div>
  );
}

export default BookList;