import React,{useState} from 'react';
import AppRoutes from './AppRoutes';
import { AuthProvider } from './contexts/AuthContext';
import { CategoryProvider } from './contexts/CategoryContext';
import { BookProvider } from './contexts/BookContext';
import { SongIndicesProvider } from './contexts/SongIndicesContext';
import UserProfile from './components/UMS/UserProfile';
import { OfflineModeProvider } from './contexts/OfflineModeContext';
import LanguageSettingsModal from './components/AppObjs/LanguageSettingsModal';
import { useLanguagePreference } from './contexts/LanguagePreferenceContext';

function App() {
  const { preferredLanguage,setPreferredLanguage } = useLanguagePreference();
  const [isProfileDropdownOpen, setIsProfileDropdownOpen] = useState(true);
  const toggleProfileDropdown = () => setIsProfileDropdownOpen(!isProfileDropdownOpen);

  const [isLanguageSettingsOpen, setIsLanguageSettingsOpen] = useState(false);
  const openLanguageSettings = () => setIsLanguageSettingsOpen(true);
  const closeLanguageSettings = () => setIsLanguageSettingsOpen(false);

  return (
    <OfflineModeProvider> 
      <AuthProvider>
        <div className="app-layout"> {/* Optional layout div */}
          <div className="flex justify-end p-4 bg-gray-100">
            <UserProfile 
            toggleDropdown={toggleProfileDropdown} 
            isDropdownOpen={isProfileDropdownOpen}
            openLanguageSettings={openLanguageSettings}  />
          </div>
          <LanguageSettingsModal
          isOpen={isLanguageSettingsOpen}
          onClose={closeLanguageSettings}
          onLanguageSave={setPreferredLanguage} 
          currentLanguage={preferredLanguage}
          />
          <CategoryProvider>
        <BookProvider>
          <SongIndicesProvider>
              <AppRoutes />
          </SongIndicesProvider>
        </BookProvider>
        </CategoryProvider>        
        </div>
      </AuthProvider>
      </OfflineModeProvider>
  );
}

export default App;