import React, { createContext, useState, useContext, useEffect, useCallback } from 'react';
import { useParams } from 'react-router-dom';

interface SongIndex {
  bookId: string; // Added bookId
  id: string;
  title: string;
  source?: string;
}

interface SongIndicesContextType {
  songIndices: SongIndex[];
  loadSongIndices: (bookId: string) => Promise<void>;
  selectedSongIndexId: string | null;
  setSelectedSongIndexId: React.Dispatch<React.SetStateAction<string | null>>;
  loading: boolean;
  error: string | null;
  currentBookId: string | null;
}

const SongIndicesContext = createContext<SongIndicesContextType>({
  songIndices: [],
  loadSongIndices: async () => {},
  selectedSongIndexId: null,
  setSelectedSongIndexId: () => {},
  loading: false,
  error: null,
  currentBookId: null,
});

interface SongIndicesProviderProps {
  children: React.ReactNode;
}

export const SongIndicesProvider: React.FC<SongIndicesProviderProps> = ({ children }) => {
  const [songIndices, setSongIndices] = useState<SongIndex[]>([]);
  const [selectedSongIndexId, setSelectedSongIndexId] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const { bookId } = useParams();
  const [currentBookId, setCurrentBookId] = useState<string | null>(null);

  const loadSongIndices = useCallback(async (bookId: string) => {
    setLoading(true);
    setError(null);
    setSongIndices([]);
    setCurrentBookId(bookId);
    await new Promise(resolve => setTimeout(resolve, 500));
    if (bookId === 'book1') {
      setSongIndices([
        { bookId: 'book1', id: 'si1', title: 'Song One', source: 'local' },
        { bookId: 'book1', id: 'si2', title: 'Song Two', source: 'google' },
      ]);
    } else if (bookId === 'book2') {
      setSongIndices([{ bookId: 'book2', id: 'si3', title: 'Song Three', source: 'local' }]);
    } else {
      setSongIndices([]);
    }
    setLoading(false);
  }, [setLoading, setError, setSongIndices, setCurrentBookId]);

  useEffect(() => {
    if (bookId) {
      loadSongIndices(bookId);
    }
  }, [bookId, loadSongIndices]);

  const value: SongIndicesContextType = {
    songIndices,
    loadSongIndices,
    selectedSongIndexId,
    setSelectedSongIndexId,
    loading,
    error,
    currentBookId,
  };

  return (
    <SongIndicesContext.Provider value={value}>{children}</SongIndicesContext.Provider>
  );
};

export const useSongIndices = () => useContext(SongIndicesContext) as SongIndicesContextType;